* {
  margin: 0;
}

a:hover {
  text-decoration: none;
}

.hello {
  margin: 0 auto;
  max-width: 85%;
  overflow: hidden;
}

.colorWhite {
  color: white;
}

.navbar a:hover {
  color: rgb(255, 255, 255);
}

.navbar-brand {
  color: white;
  font-size: 25px;
  font-weight: bold;
  font-family: "poppins", sans-serif, sans-serif;
  /* margin-top: -30px; */
  /* margin-left: -100px; */
}

.nav-btn {
  padding: 10px 20px;
  border: none;
  border-radius: 20px;
  font-weight: bold;
  font-size: 15px !important;
  font-family: "poppins", sans-serif;
  box-shadow: 3px 0px 11px 4px rgb(206, 151, 223);
  background-color: white;
  color: black;
}

.nav-btn:hover {
  font-size: 15px !important;
  background-color: #2c0659;
  color: white !important;
}

.primary-btn {
  padding: 10px 20px;
  border: none;
  border-radius: 20px;
  font-weight: bold;
  font-size: 15px;
  font-family: "poppins", sans-serif;
  box-shadow: 3px 0px 11px 4px rgb(206, 151, 223);
}

.primary-btn:hover {
  border: 2px solid white;
  background-color: #2e0559;
  color: white;
  cursor: pointer;
}

.hero-heading {
  color: rgb(255, 255, 255);
  font-size: 38px;
  /* font-family: "poppins", sans-serif; */
  font-family: monospace;
  font-weight: bold;
  width: 90%;
  margin-top: 90px;
}

.hero-subHeading {
  color: rgb(153, 153, 153);
  max-width: 90%;
}

.hero-img {
  border-radius: 30px;
  /* width: 500px; */
  float: right;
  margin-top: -40px;
  margin-right: -50px;
  /* box-shadow: 3px 0px 11px 4px rgb(113, 47, 133); */
  /* box-shadow: 3px 0px 11px 4px rgb(230, 230, 230); */
}

.topNavBar {
  display: flex;
  justify-content: space-between;
}

.mrpaypalogo {
  margin-bottom: -20px;
  margin-top: -40px;
  margin-left: -16px;
  width: 170px;
}

.navbar-light .navbar-nav .active>.nav-link, .navbar-light .navbar-nav .nav-link.active, .navbar-light .navbar-nav .nav-link.show, .navbar-light .navbar-nav .show>.nav-link {
  position: absolute;
  right: 1px;
  margin-top: -20px;
  color: white;
}

.navbar-light .navbar-toggler {
  border-color: white !important;
}

.cryptoMobileHeading {
  color: white;
  font-size: 60px;
  text-align: center;
  margin-top: 80px;
}

.cryptoMobileSubHeading {
  color: grey;
  width: 55%;
  margin: 0 auto;
  text-align: center;
  font-size: 26px;
}

.tbackgrounImg {
  background-image: url(./img/back-img.png);
}

#myId {
  border: 0;
  margin: 0 auto;
  display: block;
  border-radius: 10px;
  max-width: 1200px;
  box-shadow: 3px 0px 11px 4px rgb(255, 255, 255);
  height: 660px;
  width: 100%;
}

.bottomnavbar {
  /* box-shadow: 3px 0px 11px 4px rgb(255, 255, 255); */
  /* background-color: #280657; */
  border-top: 1px solid rgb(44, 80, 48);
  padding-top: 20px;
  background-image: linear-gradient(100deg, #000000 20%, #002008 40%, #000000 100%);
  /* overflow: hidden !important; */
}

.liveBoxImg {
  width: 30px;
  /* margin-left: -30px;
  margin-right: -20px; */
}

.owl-carousel .owl-item img {
  width: 30px !important;
}

.livePriceBox {
  width: 200px;
  border: 1px solid #290c33;
  /* box-shadow: 0.2px 0px 4px 2px rgb(255, 255, 255); */
  margin: 0 auto;
  padding: 10px 15px;
  padding-right: 25px;
  color: rgb(37, 35, 37);
  /* animation */
  transition: 0.5s;
  transform-origin: right top;
  border-radius: 10px;
}

.livePriceBox:hover {
  transform: translate(0px, -18px);
  color: white;
  border: 2px solid rgb(189, 158, 197);
}

.moreCrypto {
  font-size: 45px;
}

.LiveRates {
  position: absolute;
  left: -20px;
  background-color: yellow;
  color: #000;
  padding: 7px;
  padding-left: 40px;
  padding-right: 20px;
  cursor: pointer;
  position: fixed;
  border-radius: 30px;
  font-family: "Times New Roman", Times, serif;
  font-size: 20px;
  z-index: 99;
}

.fa-arrow-right {
  margin-left: 10px;
}

.fa-times {
  position: absolute;
  right: 20px;
  margin-top: -110px;
  font-size: 20px;
  color: white;
  border: 2px solid #eee;
  border-radius: 50%;
  padding: 5px 7px;
  cursor: pointer;
}

.m2Picture {
  float: right;
}

.navlinks {
  margin-left: 30px;
  font-size: 20px;
}

.whyChoose, .buyBitcoin, .doMoreDiv, .track, .simple {
  width: 80%;
  margin: 0 auto;
}

.simple {
  margin-left: 200px;
}

.whyChooseImg {
  margin-top: -50px;
  float: right;
  margin-right: -100px;
  width: 800px;
}

.buyBitcoinVideo {
  width: 350px;
  border-radius: 40px;
  float: left;
}

.navlinks:hover {
  color: #9c0e8f;
  border-bottom: 2px solid #9c0e8f;
}
.BinanceWidgetDiv{
  height: 72vh;
  margin-top: -100px;
}
.binance-fiat-widget {
  overflow: hidden;
  background-color: #e2e2e2;
  /* background-color: rgba(226, 226, 226, 0.4); */
  box-shadow: 0px 0px 19px rgb(255, 255, 255) !important;
  width: 60% !important;
  margin: 0 auto;
  margin-top: 50px;
}
.binance-fiat-widget svg {
  display: none;
}
.m1Pic {
  width: 220px;
  margin-top: 90px;
}

.pPic {
  width: 410px;
}

.mBothPics {
  display: inline;
}

.m1Txt {
  margin-top: 100px;
}

.navbar-toggler {
  outline: 1px dotted;
  outline: 5px auto white;
  padding: 8px 10px;
}

